@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
body {
  margin: 0;
  min-width: 320px;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  color: #000000;
  background-color: #ffffff;
}
a {
  display: block;
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (min-width: 1440px) {
  .page__body {
    position: relative;
  }
}
@media (min-width: 768px) {
  .page-header__wrapper {
    padding: 24px 30px 0;
  }
}
@media (min-width: 1440px) {
  .page-header__wrapper {
    width: 1220px;
    margin: 0 auto;
    padding: 0;
    padding-top: 54px;
  }
  .page-header__wrapper--main {
    position: absolute;
    width: 1220px;
    left: 0;
    right: 0;
    margin: auto;
    padding: 51px 0;
    box-sizing: border-box;
    background-color: transparent;
  }
}
.page-header__logo-link {
  margin-left: 20px;
  align-items: center;
  transform: translate(1px, -2px);
}
.page-header__logo-link:hover .logo__icon {
  opacity: 60%;
}
.page-header__logo-link:focus {
  outline: none;
}
.page-header__logo-link:focus .logo__icon {
  opacity: 30%;
}
@media (min-width: 768px) {
  .page-header__logo-link {
    margin: 0;
  }
}
.page-header__main-nav {
  display: grid;
  grid-template-rows: repeat(auto-fit, minmax(65px, 1fr));
  align-items: center;
}
@media (min-width: 768px) {
  .page-header__main-nav {
    display: flex;
    justify-content: space-between;
  }
}
.main-nav__toggle {
  position: relative;
  margin-right: 20px;
  margin-left: auto;
  grid-column: 2/3;
  width: 34px;
  height: 38px;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
}
.main-nav__toggle:hover {
  opacity: 60%;
}
.main-nav__toggle:focus {
  opacity: 30%;
  outline: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__list {
  position: absolute;
  top: 65px;
  z-index: 1;
  width: 100%;
  min-width: 320px;
  grid-column: 1/3;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  list-style: none;
}
@media (min-width: 768px) {
  .main-nav__list {
    position: static;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 442px;
    justify-content: space-between;
  }
}
@media (min-width: 1440px) {
  .main-nav__list {
    width: 480px;
    background-color: transparent;
  }
}
.main-nav__item {
  padding: 21px 0 23px;
  border-top: 1px solid #e6e6e6;
}
.main-nav__item:last-child {
  border-bottom: 1px solid #e6e6e6;
}
@media (max-width: 767px) {
  .main-nav__item--active-main {
    position: relative;
  }
  .main-nav__item--active-main::after {
    position: absolute;
    content: "";
    width: 76px;
    left: 50%;
    transform: translateX(-50%);
    bottom: 8px;
    height: 2px;
    background: #68b738;
  }
  .main-nav__item--active-catalog {
    position: relative;
  }
  .main-nav__item--active-catalog::after {
    position: absolute;
    content: "";
    width: 181px;
    left: 50%;
    transform: translateX(-50%);
    bottom: 8px;
    height: 2px;
    background: #68b738;
  }
  .main-nav__item--active-form {
    position: relative;
  }
  .main-nav__item--active-form::after {
    position: absolute;
    content: "";
    width: 180px;
    left: 50%;
    transform: translateX(-50%);
    bottom: 8px;
    height: 2px;
    background: #68b738;
  }
}
@media (min-width: 768px) {
  .main-nav__item {
    padding: 0;
    border: none;
  }
  .main-nav__item:last-child {
    border: none;
  }
  .main-nav__item--active {
    position: relative;
  }
  .main-nav__item--active::after {
    position: absolute;
    content: "";
    left: 0;
    right: 0;
    transform: translateX(0);
    bottom: 2px;
    height: 2px;
    background: #68b738;
  }
}
@media (min-width: 1440px) {
  .main-nav__item--active-main::after {
    background: #ffffff;
  }
}
.main-nav__link {
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
}
.main-nav__link:hover {
  color: #000000;
  opacity: 60%;
}
.main-nav__link:focus {
  color: #000000;
  opacity: 30%;
  outline: none;
}
@media (min-width: 768px) {
  .main-nav__link {
    padding: 18px 0 12px;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 24px;
  }
}
@media (min-width: 1440px) {
  .main-nav__link {
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 30px;
  }
  .main-nav__link--main {
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 30px;
    color: #ffffff;
  }
  .main-nav__link--main:hover {
    color: #ffffff;
    opacity: 60%;
  }
  .main-nav__link--main:focus {
    color: #ffffff;
    opacity: 30%;
    outline: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 9px;
  right: 0;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: 0 8px 0 0 rgba(0, 0, 0, 0.3), 0 16px 0 0 rgba(0, 0, 0, 0.3);
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  right: 0;
  width: 25px;
  height: 1.5px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: rgba(0, 0, 0, 0.3);
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__item {
    display: none;
  }
  .main-nav--nojs .main-nav__item {
    display: block;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav__wrapper {
    position: absolute;
    top: 65px;
    left: 0;
    width: 100%;
    min-width: 320px;
    background-color: #ffffff;
  }
  .main-nav--nojs .main-nav__list {
    position: static;
  }
}
